/**
 * DatePickerV2.scss
 * Custom form dropdown menu with input
 *
 * @version 0.0.1
 * @author [Wilson Phoo](wilson@policypal.com)
 */
 @import '../../../base';
.dateinner {
  position: fixed;
  width: 310px;
  z-index: 100000;
  // margin: 10px 0;

  .close-button-container {
    display: none !important;
    @media screen and (max-width: 576px){
      display: flex !important;
      justify-content: flex-end;
    }
  }

  .close-icon {
    width: 18px;
    height: 18px;
    margin: 10px;
    transform: translateX(10px) translateY(-10px);
    filter: brightness(0) saturate(100%) invert(95%) sepia(7%) saturate(540%) hue-rotate(143deg) brightness(96%) contrast(85%);

    &:hover {
      transition: all 0.3s ease;
      transform: translateX(10px) translateY(-10px) scale(1.1);
      filter: brightness(0) saturate(100%) invert(54%) sepia(86%) saturate(1621%) hue-rotate(128deg) brightness(103%) contrast(101%);
    }
  }
  
  .date-inner-container {
    margin: auto;
    min-width: 310px;
    padding: 30px;
    border-radius: 8px;
    background-color: white;
    
    @media screen and (min-width: 577px){
      padding-top: 15px 20px 20px 20px;
    }

    .date-col {
      position: relative;
      width: 14.285714%;

      &:hover {
        .day-inactive {
          background-color: $color-hover-alt;
          border-radius: 50%;

          p {
            color: white;
          }
        }
      }
    }
    
    .date-inner-padding {
      padding: 0 20px;
    }
    
    p {
      font-family: 'Open Sans', sans-serif;
      font-size: $font-size-smallest;
      font-weight: $font-weight-normal;
      font-style: normal;
      font-stretch: normal;
      line-height: normal;
      letter-spacing: -0.2px;
      color: $color-caption;
      cursor: default;
    }
    
    .date-divider {
      height: 1px;
      background-color: $color-line;
      margin: auto;
      margin-bottom: 20px;
    }
    
    .day-container {
      display: flex;
      justify-content: center;
      align-items: center;
      margin: 6px 0;
      height: 30px;
      flex-wrap: wrap;
    }
    
    .date-arrow-style {
      color: $color-line;
      padding: 0;
      display: flex;
      cursor: pointer;

      .angle-left {
        transform: rotate(90deg);
        margin-left: 10px;
      }
    
      .angle-right {
        transform: rotate(-90deg);
        margin-right: 10px;
      }
    }
    
    .date-arrow-style:active {
      color: $color-hover;
    }
    
    
    .date-arrow-style:focus {
      outline: none;
    }

    .date-arrow-style:active {
      filter: $color-filter-hover-alt;
    }

    .day-container {
      .day-disabled,
      .day {
        width: 30px;
        height: 30px;
        display: flex;
        justify-content: center;
        align-items: center;
        position: absolute;
        left: 0;
      }

      .day:focus {
        outline: none;
      }

      .day p {
        color: #46737a;
        margin: 0;
        cursor: pointer;
      }

      .day-active {
        background-color: $color-hover;
        border-radius: 50%;
      }
      
      .day:active {
        background-color: $color-hover-alt;
        border-radius: 50%;
      }
      
      .day-active p {
        color: white;
      }
    }
    
    .day-range {
      height: 30px;
      width: 50%;
    }
    
    .end-active,
    .left-active ,
    .right-active {
        background: $color-form;
    }
    
    .end-active {
        border-radius: 50%;
        width: 30px;
        height: 30px;
        position: absolute;
        left: 0;
    }
    
    .blocked {
      color: $color-line !important;
    }

    .dropdown {
      width: 100%;
    }
    
    .dropdown button {
        width: 100%;
        text-align: left;
        font-size: 14px;
    }
    
    .dropdown-menu {
        max-height: 300px !important;
        overflow-y: scroll !important;
        overflow-x: hidden !important;
        transform: translate3d(0px, 36px, 0px) !important;
        min-width: 0 !important;

        .dropdown-item:active {
          background: $color-hover-alt;
          outline: none;
        }
    }
    
    .dropdown-toggle,
    .btn-outline-secondary:hover {
        background: transparent !important;
        border: solid 2px $color-line;
    }
    
    .btn-outline-secondary:focus,
    .dropdown-toggle:focus {
        box-shadow: none !important;
    }
    
    .dropdown-toggle:after {
        margin-left: 0.83em !important;
        color: $color-line;
        position: absolute;
        top: 13px;
        right: 10px;
        border: none;
        background-image: url(../../../assets/images/forms/arrow.svg);
        height: 20px;
        width: 20px;
        background-repeat: no-repeat;
        background-size: 15px;
    }
    
    .selector-left {
        display: flex;
        padding-right: 10px;
    }
    
    .selector-right {
        display: flex;
        padding-left: 0;
    }

    .btn {
        height: 36px;
        color: #354f53 !important;
        font-weight: 300;
        border-radius: 8px;
    } 
  }
  
  .date-container-closed {
    visibility: hidden;
    opacity: 0;
    width:0 !important;
    height:0 !important;
    @media screen and (min-width: 577px){
      transition: all 0.3s ease;
      border-radius: 8px;
    }
    z-index: -10;
  }

  .date-container-opened {
    visibility: visible;
    opacity: 1;
    width:100%;
    height: 100%;
    min-height: 433px;
    @media screen and (min-width: 577px){
      transition: all 0.3s ease;
      border-radius: $border-radius-main;
      background-origin: border-box;
      background-clip: content-box, border-box;
      border: solid 2px transparent;
      padding: 0;
      box-shadow: 0 2px 10px 0 rgba(0, 0, 0, 0.15);
      background-image: linear-gradient($color-form, $color-form),
      linear-gradient(101deg, $color-hover, $color-body);
    }

    &:focus {
      outline: none;
    }
  }

  .list-container {
    padding-left: 0 !important;
    padding-top: 10px;
    overflow: hidden;
    border-radius: 8px;
    background-color: white;
    @media screen and (max-width: 576px){
      border-radius: 0;
      padding-top: 10px;
      display: flex;
      background-image: linear-gradient(134deg, rgba(0, 215, 175, 0.75), rgba(70, 115, 122, 0.75));
    }
    
    height: inherit;
    min-height: 433px;
    margin: 0;
  }

  .list-closed {
    margin-top: 0;
    max-height: 0;
    @media screen and (min-width: 577px){
      transition: all 0.3s ease;
      border-radius: 8px;
    }
  }
  
  .list-opened {
    @media screen and (min-width: 577px){
      transition: all 0.3s ease;
      border-radius: 8px;
    }
  }

  .selected-container {
    align-items: center;
  }

  .arrow-up {
    position: absolute;
    margin-top: -10px;
    margin-left: 50px;
    z-index: -1;

    @media screen and (max-width: 576px){
      display: none;
    }
  }

  .arrow-down {
    position: absolute;
    margin-left: 50px;
    z-index: -1;
    bottom: -9px;
    transform: rotate(180deg);

    @media screen and (max-width: 576px){
      display: none;
    }
  }

  .date-img {
    padding-right: 10px;
    padding-bottom: 8px;
  }

  .date-img-in {
    width: 32px;
    height: 32px;
  }

  .date-img-out {
    width: 38px;
    height: 38px;
  }

  &.dateinner-opened {
    @media screen and (max-width: 576px){
      width: 100%;
      height: 100vh;
      top: 0 !important;
      left: 0 !important;
    }
  }
}
